body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.brand-logo, h2{
    font-family: 'Lobster', cursive;
}

.auth-card{
    padding: 20px;
    text-align: center;
    max-width: 400px;
    margin: 30px auto;
}

.gallery{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
}

.item{
    width: 30%;
    padding-bottom: 5px;
}

.home-card{
    max-width: 640px;
    height: -webkit-max-content;
    height: -moz-max-content;
    height: max-content;
    margin: 30px auto;
}

.post-card{
    max-width: 640px;
    padding: 20px;
    margin: 30px auto;
    text-align: center;
}


a{
    color: darkslateblue !important;
}


.btn{
    margin: 10px;
}
